import React from 'react'; 
import './App.css';
import { useEffect, useState } from 'react';
import Recipe from './Recipe';


const App = () => {
const APP_ID = "e04a5391";
const APP_KEY = "d2c7aa1867553b18ebb85391d16c25fe";
const [recipes, setRecipes] = useState([]);
const [search, setSearch] = useState([]);
const [query, setQuery] = useState('');

useEffect(() =>{
 getRecipes(); 
},[query]);

const getRecipes = async () => {
  const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`);
const data = await response.json();
setRecipes(data.hits);
console.log(data.hits);
};

const updateSearch = e => {
  setSearch(e.target.value);
  console.log(search);
};

const getSearch = e =>{
  e.preventDefault();
  setQuery(search);
  setSearch('');
}
return(
  <div className="App">
   <center><img className = "img" src = "/images/logo.png"/></center>
   <form onSubmit = {getSearch} className = "search-form">
     <input className="search-bar" placeholder = "Search your recipe here" type = "text" value = {search} onChange={updateSearch}/>
     <button className="search-button" type="submit">Search</button> 
   </form>
   <div className = "recipes">
   {recipes.map(recipe =>(
     <Recipe 
     key = {recipe.recipe.label}
     title={recipe.recipe.label} 
     calories={recipe.recipe.calories}
     image={recipe.recipe.image}
     ingredients={recipe.recipe.ingredients}
     />
   ))}</div>
  </div>
);
};

export default App;
